.button {
    // display: grid;
    background: coral;
    padding: 1em 2em;
    color: white;
    cursor: pointer;
    border: none;
    font-weight: 600;
    margin-top: 2rem;
    // margin-bottom: 2rem;
    margin-left: -70%;
    width: 20%;
    border-radius: 7px;
    font-size: 18px;
    z-index: 12;

}

.button-hover {
    background: #333;

}

.button:hover{
    background-color: rgb(117, 67, 117);
    color: coral;
    font-style: italic;

}


.modal {
    // display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 25%;
    gap: 0.4rem;
    width: 450px;
    padding: 1.2rem;
    min-height: 250px;
    position: absolute;
    top: -30%;
    left: 5%;
    outline-style: dotted;
    outline-color: coral;
    background-color: white;
    border: 4px solid #ddd;
    border-radius: 15px;
    z-index: 4;
    background-color: rgb(80, 184, 177);
}

.modal .flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.modal input {
    padding: 0.7rem 1rem;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 0.9em;
}

.inner-modal {
    background-color: peru;
    color: antiquewhite;
    border-radius: 7px;
}



.welcome {
    background-color: coral;
    padding: 1rem;
}

.paragraph {
    line-height: 2rem;
    font-weight: 500;
}

.hidden {
    display: none;
}

.modal p {

    margin: 0.4rem 0 0.2rem;
}

.modal-footer {
    background-color: coral;
    padding: 2rem;
}

.btn-close {
    margin-top: 1rem;
    transform: translate(10px, -20px);
    padding: 0.5rem 0.7rem;
    background: #eee;
    border-radius: 50%;
}

.btn-close:hover {
    background: #777;
}

.overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(3px);
    z-index: 3;
}

body {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif; 
    font-style: italic;
    color: whitesmoke;
    font-size: large;
    font-weight: 300;
    // display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
    overflow: hidden;
    // background-color: aquamarine;
}
#selector {
    background-color: rgb(80, 184, 177);
    color: whitesmoke;
    font-size: 16px;
    text-align: center;
    margin-top: 5%;
    margin-left: -70%;
    margin-bottom: 2rem;
    border-radius: 7px;
    outline-style: double;
    outline-color: coral;
    z-index: 2;
    // max-width: 40%;
    overflow: scroll;
    max-height: 90px;
    overflow-y: scroll;

}

#selector option{
    max-height: 90px;
    overflow-y: scroll; 
    width: 200px; 
    padding: 5px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    position: absolute; 
}

#selector:hover {
    cursor: pointer;
}

.country {
    fill: rgb(23, 116, 45);
    stroke: rgb(7, 70, 55);
}

path:hover {
    fill: rosybrown;
    cursor: pointer;
    
}

h1 {
    margin-top: 2rem;
    margin-right: 6rem;
    margin-bottom: 2rem;
    text-align: center;
    font-size: 4rem;
    font-family: marker felt;
    -webkit-text-stroke: 3px coral;
    font-style: oblique;
    letter-spacing: 1rem;
    position: center;
    z-index: 1;
    font-weight: 900; 
    color: rgb(80, 184, 177);
    
}



